.footer-copyright {
    background-color: #282c35;
    padding-top: 3px;
    padding-bottom: 3px;
}

.footer-copyright p {
    margin: 10px;
    color: #ccc;
}

.my-footer {
    background-color: #2c3e50;
    color: white;
}

.my-footer ul {
    list-style-type: none;
    padding-left: 0;
    line-height: 1.7;
}

.my-footer h5 {
    font-size: 18px;
    color: white;
    font-weight: bold;
    margin-top: 30px;
}

.my-footer a {
    color: #d2d1d1;
    text-decoration: none;
}

.my-footer a:hover,
.my-footer a:focus {
    text-decoration: none;
    color: white;
}

.social-networks {
    text-align: center;
    padding-top: 30px;
    padding-bottom: 38px;
}

.social-networks a {
    font-size: 32px;
    margin-right: 5px;
    margin-left: 5px;
    color: #f9f9f9;
    padding: 10px;
    transition: 0.2s;
}

.social-networks a:hover {
    text-decoration: none;
}

.social-networks .github:hover {
    color: #6f42c1;
}

.social-networks .instagram:hover {
    background: #d6249f;
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.social-networks .twitter:hover {
    color: #00aced;
}